.Balance {
  background-color: hsl(10deg, 79%, 65%);
  padding: 20px;
  border-radius: 10px;
  color: hsl(33deg, 100%, 98%);
  font-size: 15px;
  display: flex;
  justify-content: space-between;
}
@media (min-width: 750px) {
  .Balance {
    font-size: 18px;
  }
}
.Balance .Value {
  font-size: 24px;
  font-weight: bold;
  margin: 5px 0 5px 0;
}
@media (min-width: 750px) {
  .Balance .Value {
    font-size: 32px;
  }
}