@import 'variables';
@import 'responsive';

.Balance {
    background-color: $SoftRed;
    padding: 20px;
    border-radius: 10px;

    color: $VeryPaleOrange;

    font-size: 15px;

    display: flex;
    justify-content: space-between;

    @include desktop {
        font-size: 18px;
    }

    .Value {
        font-size: 24px;
        font-weight: bold;
        margin: 5px 0 5px 0;

        @include desktop {
            font-size: 32px;    
        }
    }
}