.Spending {
  background-color: hsl(33deg, 100%, 98%);
  border-radius: 10px;
  padding: 21px;
}
.Spending h1 {
  font-weight: bold;
  font-size: 24px;
  padding: 9px 0;
}
@media (min-width: 750px) {
  .Spending h1 {
    font-size: 32px;
  }
}
.Spending hr {
  border: 1px solid hsl(27deg, 66%, 92%);
  margin: 10px 0;
}
.Spending .Totals {
  display: flex;
  font-size: 15px;
  letter-spacing: 0.01rem;
  color: hsl(28deg, 10%, 53%);
  justify-content: space-between;
  align-items: flex-end;
}
@media (min-width: 750px) {
  .Spending .Totals {
    font-size: 18px;
  }
}
.Spending .Totals .Totals_text {
  margin: 5px 0;
}
.Spending .Totals .Totals_dollars {
  font-weight: bold;
  font-size: 29px;
  color: hsl(25deg, 47%, 15%);
}
@media (min-width: 750px) {
  .Spending .Totals .Totals_dollars {
    font-size: 46px;
  }
}
.Spending .Totals .Totals_percent {
  color: hsl(25deg, 47%, 15%);
  font-weight: bold;
}
.Spending .Totals .Totals_right {
  text-align: end;
}
.Spending .Graph {
  height: 225px;
  width: 325px;
}
@media (min-width: 750px) {
  .Spending .Graph {
    width: 540px;
  }
}