@import 'variables';
@import 'responsive';

.Spending {
    background-color: $VeryPaleOrange;

    border-radius: 10px;
    padding: 21px;

    h1 {
        font-weight: bold;
        font-size: 24px;
        padding: 9px 0;

        @include desktop {
            font-size: 32px;
        }
    }

    hr {
        border: 1px solid $Cream;
        margin: 10px 0;
    }

    .Totals {
        display: flex;
        font-size: 15px;
        letter-spacing: 0.01rem;
        color: $MediumBrown;
        justify-content: space-between;
        align-items: flex-end;

        @include desktop {
            font-size: 18px;
        }

        .Totals_text {
            margin: 5px 0;
        }

        .Totals_dollars {
            font-weight: bold;
            font-size: 29px;
            color: $DarkBrown;

            @include desktop {
                font-size: 46px;
            }
        }

        .Totals_percent {
            color: $DarkBrown;
            font-weight: bold;
        }

        .Totals_right {
            
            text-align: end;
        }
    }

    .Graph {
        height: 225px;
        width: 325px;

        @include desktop {
            width: 540px;
        }
    }

}